* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  display: inline-block;
  background: #fff;
  border: 1px solid #999;
  margin: 0;
  padding: 0;
  width: 34px;
  height: 34px;
}

.board {
  width: 100%;
  height: 70vh;
  overflow: scroll;
  white-space: nowrap;
  display: flex;
  flex-direction: column;
  font-size: 0;
}

.board-row {
  display: inline-block;
}

.board-row .square:not(:first-child) {
  border-left: 0;
}

.board-row:not(:first-child) {
  margin-top: -1px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}

.menus {
  display: flex;
  gap: 0.5rem;
}

.game {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.game-info {
  margin-left: 20px;
}
